@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --container: 800px;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Oswald", sans-serif;
  background: url(/static/media/bg1.1b2ec185.jpg);
  background-size: cover;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.logo {
  background: #fff;
  max-width: 800px;
  max-width: var(--container);
  margin: 0 auto;
}
.container {
  background: rgba(247, 227, 222, 0.9);
  max-width: 800px;
  max-width: var(--container);
  margin: 0 auto;
  min-height: 100vh;
  width: 100%;
}
.movie-info,
.poster-img {
  display: inline-block;
  width: calc(50% - 10px);
  vertical-align: top;
  text-align: left;
}
.movie-info {
  padding: 10px;
}
.search-box {
  display: flex;
}
.search {
  width: 100%;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding: 5px;
  font-size: 20px;
}
.btn {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: #bb837c;
  box-shadow: none;
  border: 0;
  color: #fff;
  font-weight: bold;
  padding: 5px 20px;
}
.movie-title {
  font-size: 35px;
}
.release-year {
  display: inline-block;
  font-size: 20px;
  background: #c89f9b;
  padding: 5px;
}
.country {
  font-size: 20px;
}
p {
  margin-bottom: 10px;
}

@media only screen and (max-width: 768px) {
  .movie-info,
  .poster-img {
    width: 100%;
  }
}

